body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

main { 
  position: relative;
}

.footer-section {
  border-top: #dfdddd solid 1px; }

.footer-wrapper {
  padding-top: 40px;
  padding-bottom: 10px;
  margin: 0 auto;
  max-width: 980px;
  background: #ffffff; }

.footer-container {
  padding-left: 20px;
  padding-right: 20px; }

.footer-menu {
  display: -webkit-flex;
  display: flex;
  /* justify-content: center; */
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-flex-direction: row;
          flex-direction: row;
  align-items: stretch;
  width: 100%;
  max-width: 1920px; }

.footer-info-wrapper {
  height: 100%;
  word-break: break-all;
  border-top: #dfdddd solid 1px; }

.footer-info-container {
  overflow: hidden;
  padding-top: 20px;
  padding-bottom: 10px; }

.footer-logo-wrapper {
  float: left;
  height: 100%;
  padding-top: 6px; }
  .footer-logo-wrapper a {
    color: black; }

.footer-info {
  float: right;
  display: -webkit-flex;
  display: flex;
  height: 100%; }

.divider {
  border-bottom: none;
  border-color: #bdbdbd; }

select {
  -moz-appearance: none;
  /* Firefox */
  -webkit-appearance: none;
  /* Safari and Chrome */
  appearance: none;
  color: #6d6d6d; }

.form-group {
  background: transparent;
  padding-left: 3px;
  padding-right: 3px; }

.option {
  padding: 15px;
  color: black; }

.form-control {
  border: #6d6d6d solid 1px;
  border-radius: 3px;
  background: transparent;
  padding: 5px; }
  .form-control:active {
    outline: none;
    background: transparent; }
  .form-control:focus {
    outline: none;
    background: transparent; }

ul {
  padding: 0; }

li {
  list-style: none; }

a {
  text-decoration: none; }

.footer-section-menu {
  padding: 0; }

.footer-submenu-container {
  -webkit-flex-basis: 40%;
          flex-basis: 40%; }

.submenu-header {
  font-size: 16px;
  font-weight: 500;
  color: #3a3a3a; }

.submenu-list-section .menu-list-item {
  font-size: 13px;
  font-weight: 300;
  color: #464646; }
  .submenu-list-section .menu-list-item:hover {
    text-decoration: underline; }

.menu-list {
  padding-bottom: 3px; }

.gallery {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center; }

.column {
  height: 600px;
  width: 500px;
  position: relative;
  overflow: hidden; }

.image-wrap {
  position: relative;
  height: 50%; }

.wrap-left {
  margin-right: 5px;
  margin-bottom: 5px; }

.left {
  width: 40%; }

.right {
  width: 60%; }

.wrap-big {
  height: 100% !important;
  margin-left: 0 !important;
  /* overflow: hidden; */ }

.image-wrap:hover > .btn-visibility {
  visibility: visible; }

.image-wrap > .btn-visibility {
  visibility: hidden; }

.image-content {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1; }

.image-description-wrap {
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  padding: 25px;
  pointer-events: none; }

.image-title {
  margin: 0;
  color: white;
  font-weight: 500;
  font-size: 18px; }

.image-subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 15px;
  margin: auto;
  width: 80%;
  color: white; }

h4,
h3 {
  margin: 0;
  padding: 0; }

.email-section {
  background: #fbfbfb;
  padding: 50px 20px; }

.email-section-container {
  margin-left: 30px;
  margin-right: 30px; }

.inline-section {
  display: inline-block;
  width: 48%; }

.form-header {
  font-weight: 600;
  font-size: 20px;
  color: #960000; }

.form-subheader {
  font-weight: 400;
  font-size: 12px;
  margin-top: 10px;
  color: #2b2b2b; }

.form-wrapper {
  position: absolute;
  margin-left: 20px; }

.input-form {
  margin-right: 10px;
  background: white;
  border: #dfdfdf solid 1px;
  border-radius: 20px;
  padding: 10px 30px;
  max-width: 200px;
  box-shadow: 5px 5px 5px 0px rgba(222, 222, 222, 0.46); }
  .input-form textarea:focus, .input-form:focus {
    outline: none; }

.form-btn {
  font-weight: 500;
  padding: 10px 20px;
  border: none;
  background: #e43939;
  color: white;
  border-radius: 20px;
  box-shadow: 5px 5px 5px 0px rgba(222, 222, 222, 0.46); }
  .form-btn:hover {
    background: #c42e2e; }
  .form-btn:active {
    background: #ac2a2a; }
  .form-btn:focus {
    outline: none; }

.prev-slider-btn {
  display: block;
  left: -8px; }

.next-slider-btn {
  display: block;
  right: -8px; }

